<template>
    <div class="customerquestion_main">
        <p class="questionTitle">{{ initQuestionData.title }}</p>
        <p class="numberCase">
            <span
                :class="[itemSelection == item ? 'number_main_hover' : 'number_main']"
                v-for="(item,index) in initQuestionData.listData" 
                :key="index"
                @click="itemClick(item)"
            >{{item}}</span>
        </p>
    </div>
</template>

<script>
export default {
    /**
     * initQuestionData:{
     *      itemSelection:'', //默认选中项
     *      title:'', //问题列表标题
     *      listData:[''] //展示的数据
     * }
     */
    name:'customerQuestion',
    props:['initQuestionData'],
    data(){
        return{
            itemSelection:this.initQuestionData.itemSelection
        }
    },
    mounted(){
        
    },
    methods:{
        //点击某一项时出发，$emit触发当前组件上的事件 getData 并将当前点击的值传给parent
        itemClick(item){
            this.itemSelection = item;
            this.$emit('getData',item);
        }
    }
}
</script>

<style scoped>
    .questionTitle{
        font-size:0.16rem;
        text-align: left;
    }
    .numberCase{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
    }
    .number_main{
        width: 0.4rem;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        border-radius: 50%;
        margin-right: 0.1rem;
    }
    .number_main_hover{
        width: 0.4rem;
        height: 0.4rem;
        line-height: 0.4rem;
        text-align: center;
        border-radius: 50%;
        margin-right: 0.1rem;
        background-color:#c22e49;
        color:#fff;
    }
</style>